const LessonImage = ({ image, caption }) => {
   const error = <h3><span role='img' aria-label='danger'></span> Image missing</h3>

   return (
      <div className='lesson-module lesson-module-image'>
         {(image && image.url)
            ? <figure className='lesson-module-image__figure'><img className='lesson-module-aboutus-image__large' src={image.url} alt={image.title} />
            {caption && <figcaption className='lesson-module-image__caption'><b>{caption}</b></figcaption>}
            </figure>
            : error
         }
      </div>
   )
}

