import React from 'react';
// import Link from '@material-ui/core/Link';
import { Link } from 'react-router-dom';

import { makeStyles } from '@material-ui/core/styles';
import { Grid, Button, Typography } from '@material-ui/core';
import Hidden from '@material-ui/core/Hidden';

// import footerAdornment from "@material-ui/icons/Home";
import FacebookIcon from '@material-ui/icons/Facebook';
import TwitterIcon from '@material-ui/icons/Twitter';
import InstagramIcon from '@material-ui/icons/Instagram';
import ParticlesBg from 'particles-bg';


const useStyles = makeStyles(theme => ({
    footer:{
        "(min-width: 1919px) and (max-width: 2560px)": {
            backgroundColor: theme.palette.common.blue,
            width: "100%",
            zIndex: 102,
            position: "relative"
        }, "@media ((min-width: 410px) and (max-width: 915px)) ": {
            backgroundColor: theme.palette.common.blue,
            width: "100%",
            height:"120% !important",
            zIndex: 102,
            position: "relative"
        }
    },
    // adornment: {
    //     width: "25em",
    //     verticalAlign: "bottom",
    //     [theme.breakpoints.down("md")]: {
    //         width: "21em"
    //     },
    //     [theme.breakpoints.down("xs")]: {
    //         width: "15em"
    //     }
    // },
    mainContainer: {
        "@media ((min-width: 1919px) and (max-width: 2560px))" : {
        position: "relative",
        marginTop: "-25em",
        background: 'linear-gradient(145deg, rgb(255 43 43 / 90%), rgb(187 22 22 / 60%)) !important',
        height:"600px",
        }, 
        "@media (min-width: 410px) and (max-width: 915px)": {
        position: "relative",
        marginTop: "15em",
        background: 'linear-gradient(145deg, rgb(255 43 43 / 90%), rgb(187 22 22 / 60%)) !important',
        height:"1600px",
        }
    },
    link: {
        // color: "white",
        fontFamily: "Arial",
        fontSize: "0.75rem",
        fontWeight: "bold",
        textDecoration: "none",//For removing the underline from the links in the footer.
        margin:"0.5em",
        padding:"0.5em",
    },
    gridItem: {
        margin: "3em"
    },
    icon: {
        height: "4em",
        weight: "4em",
        [theme.breakpoints.down("xs")]: {
            height: "2.5em",
            width: "2.5em"
        }
    },
    socialContianer: {
        position: "relative",
        marginTop: "-5em",
        right: "1.5em",
        [theme.breakpoints.down("xs")]: {
            right: "4.6em"
        }
    }
}));

export default function Footer(props) {
    const classes = useStyles();
    const { value, setValue, selectedIndex, setSelectedIndex } = props;//The Footer components is getting props from App component in moudle App.js
    //mdDown is for media with screen size medium and smaller. Breakpoint for smaller screens to hide the links in the footer.
    console.log(value,selectedIndex);
    return (
        <footer className={classes.footer}>
            <Hidden >
                <Grid container justify="center" className={classes.mainContainer}>
                    <Grid item className={classes.gridItem}>
                        <Grid container direction="column" spacing={2}>
                            <Button component={Link} to="/" className={classes.link} onClick={() => setValue(0)}>
                                Home
                        </Button>
                        </Grid>
                    </Grid>
                    <Grid item className={classes.gridItem}>
                        <Grid container direction="column" spacing={2}>
                            <Button component={Link} to="/cloud" className={classes.link} onClick={() => { setValue(1); setSelectedIndex(0) }}>Cloud Services</Button>
                            <Button component={Link} to="/CustomSWD" className={classes.link} onClick={() => { setValue(1); setSelectedIndex(1) }}>Custom Software Development</Button>
                            <Button component={Link} to="/mobile" className={classes.link} onClick={() => { setValue(1); setSelectedIndex(2) }}>Mobile App Development</Button>
                            <Button component={Link} to="/research" className={classes.link} onClick={() => { setValue(1); setSelectedIndex(3) }}>AI Development</Button>
                        </Grid>
                    </Grid>
                    <Grid item className={classes.gridItem}>
                        <Grid container direction="column" spacing={2}>
                            <Button component={Link} to="/revolution" className={classes.link} onClick={() => setValue(2)}>The Revolution</Button>
                            <Button component={Link} to="/revolution" className={classes.link} onClick={() => setValue(2)}>Vision</Button>
                            <Button component={Link} to="/revolution" className={classes.link} onClick={() => setValue(2)}>Technology</Button>
                            <Button component={Link} to="/revolution" className={classes.link} onClick={() => setValue(2)}>Process</Button>
                        </Grid>
                    </Grid>
                    <Grid item className={classes.gridItem}>
                        <Grid container direction="column" spacing={2}>
                            <Button component={Link} to="/aboutus" className={classes.link} onClick={() => setValue(3)}>about Us</Button>
                            <Button component={Link} to="/aboutus" className={classes.link} onClick={() => setValue(3)}>History</Button>
                            <Button component={Link} to="/aboutus" className={classes.link} onClick={() => setValue(3)}>Team</Button>
                        </Grid>
                    </Grid>
                    <Grid item className={classes.gridItem}>
                        <Grid container direction="column" spacing={2}>
                            <Button component={Link} to="/contactus" className={classes.link} onClick={() => setValue(4)}>Contact Us</Button>
                        </Grid>
                    </Grid>
                </Grid>
            </Hidden> 

            <Grid container justify="flex-end" spacing={2} className={classes.socialContianer}>
                <Grid item component={"a"} href="https://facebook.com" rel="noopener noreferrer" target="_blank">
                    {/* <img alt="facebook logo" src={{FacebookIcon}} className={classes.icon} rel="noopener noreferrer" target="_blank" /> */}
                    <FacebookIcon />
                </Grid>
                <Grid item component={"a"} href="https://twitter.com">
                    {/* <img alt="twitter logo" src={TwitterIcon} className={classes.icon} rel="noopener noreferrer" target="_blank" /> */}
                    <TwitterIcon/>
                </Grid>
                <Grid item component={"a"} href="https://www.instagram.com">
                    {/* <img alt="instagram logo" src={InstagramIcon} className={classes.icon} rel="noopener noreferrer" target="_blank" /> */}
                    <InstagramIcon/>
                </Grid>
            </Grid>
            <Typography variant="body2">Om Sri Sri LalithaMahaTripuraSundariDevi Namah</Typography>
            <ParticlesBg  type="lines" bg={true} />
        </footer>
    );
}